import React, { Component, Fragment } from 'react';

class Resume extends Component {

  sortSkills(skills){
    return skills.sort()
 }

  getEmploymentDetails(jobs){
    return(
      <Fragment>
      {
        jobs.map(job => {
          return(
            <div key={job.company}>
            <h3>{job.company}</h3>
            <p className="info">{job.title}<span>&bull;</span> <em className="date">{job.years}</em></p>
            <p>{job.description}</p>
            <p><b><i>Technologies:</i> </b>{this.sortSkills(job.skills).join(", ")}</p>
        </div>
          )
        })
      }
      </Fragment>
    )
  }

  getEducationDetails(education){
    return(
      <Fragment>
      {education.map(school => {
        return(
          <div key={school.school}><h3>{school.school}</h3>
          <p className="info">{school.degree} <span>&bull;</span><em className="date">{school.graduated}</em></p>
          <p>{school.description}</p>
          <p>{this.sortSkills(school.skills).join(", ")}</p>
          </div>
        )
      })}
      </Fragment>
      )
  }

  render() {
    if(this.props.data){
      const {work, education} = this.props.data;
      return (
        <section id="resume">


        <div className="row work">
  
           <div className="three columns header-col">
              <h1><span>Work</span></h1>
           </div>
  
           <div className="nine columns main-col">
            {this.getEmploymentDetails(work)}
          </div>
      </div>
  
        <div className="row education">
           <div className="three columns header-col">
              <h1><span>Education</span></h1>
           </div>
  
           <div className="nine columns main-col">
              <div className="row item">
                 <div className="twelve columns">
                   {this.getEducationDetails(education)}
                 </div>
              </div>
           </div>
        </div>
     </section>
      );
    }

    return null
  }
}

export default Resume;
