import React, { Component } from 'react';

class Portfolio extends Component {
  render() {

    if(this.props.data){
      let { projects } = this.props.data;
      let entries = projects.map(function(projects){
        let projectImage = 'images/'+projects.image;
        return <div key={projects.title} className="columns portfolio-item">
           <div className="item-wrap">
            <a href={projects.url} title={projects.title}>
               <img className="project-icon" alt={projects.title} src={projectImage} />
               <div className="overlay">
                  <div className="portfolio-item-meta">
                 <h5>{projects.title}</h5>
                     <p>{projects.category}</p>
                  </div>
                </div>
              <div className="link-icon"><i className="fa fa-link"></i></div>
            </a>
          </div>
        </div>
      })
      return (
        <section id="portfolio">
          <div className="row">
            <div className="twelve columns collapsed">
                <h1>Below are just some of the brands I've had the pleasure of working with.</h1>
                <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                    {entries}
                </div>
              </div>
          </div>
        </section>
      );
    }
  return null;    
  }
}

export default Portfolio;
